<template>
  <v-form ref="form" v-model="valid" @submit.prevent="onSubmit">
    <v-container>
      <v-row>
        <v-col cols="6">
          <v-text-field
            label="Brand"
            placeholder="Brand"
            v-model="center.brand"
            :rules="rules.brand"
            autofocus
          />

          <v-text-field
            label="Angle up"
            placeholder="Angle up"
            type="number"
            v-model="center.angle_up"
            :rules="rules.angle_up"
          />
          <v-text-field
            label="ILF-B distance up"
            placeholder="ILF-Berger distance up"
            type="number"
            v-model="center.ilf_berger_distance_up"
            :rules="rules.ilf_berger_distance_up"
          />
          <v-text-field
            label="Weight"
            placeholder="Weight"
            type="number"
            v-model="center.weight"
            :rules="rules.weight"
          />
        </v-col>
        <v-col cols="6">
          <v-text-field
            label="Model"
            placeholder="Model"
            v-model="center.model"
            :rules="rules.model"
          />
          <v-text-field
            label="Angle down"
            placeholder="Angle down"
            type="number"
            v-model="center.angle_down"
            :rules="rules.angle_down"
          />
          <v-text-field
            label="ILF-B distance down"
            placeholder="ILF-Berger distance down"
            type="number"
            v-model="center.ilf_berger_distance_down"
            :rules="rules.ilf_berger_distance_down"
          />
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <v-btn type="submit" color="primary" class="white--text mr-4" :disabled="!valid">Save</v-btn>
          <v-btn class="mr-4" @click="onCancel">Cancel</v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import { required, isNumber } from "@/libs/validations";

export default {
  props: ["item"],
  mounted() {
    if (this.item) this.center = { ...this.item };
  },
  data() {
    return {
      valid: false,
      center: {
        brand: "",
        model: "",
        weight: "",
        angle_up: "",
        angle_down: "",
        ilf_berger_distance_up: "",
        ilf_berger_distance_dowm: ""
      },
      rules: {
        brand: [required],
        model: [required],
        weight: [required, isNumber],
        angle_up: [required, isNumber],
        angle_down: [required, isNumber],
        ilf_berger_distance_up: [required, isNumber],
        ilf_berger_distance_down: [required, isNumber]
      }
    };
  },
  methods: {
    async onSubmit() {
      this.$emit("onSubmit", { ...this.center });
      // this.person.name = "";
      this.resetForm();
    },

    async onCancel() {
      this.$emit("onCancel");
      this.resetForm();
    },

    resetForm() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>
